<template>
  <div class="container">
    <div id="amap-container">
    </div>
    <div class="citySelect-container">
      <el-select v-model="provinceIndex" v-on:change="search(provinceIndex, 'province')" placeholder="请选择省">
        <el-option
          v-for="(item, index) in provinceList"
          :key="item.adcode"
          :label="item.name"
          :value="index">
        </el-option>
      </el-select>
      <el-select v-model="cityIndex" v-on:change="search(cityIndex, 'city')" placeholder="请选择市">
        <el-option
          v-for="(item, index) in cityList"
          :key="item.adcode"
          :label="item.name"
          :value="index">
        </el-option>
      </el-select>
      <el-select v-model="districtIndex" v-on:change="search(districtIndex, 'district')" placeholder="请选择区">
        <el-option
          v-for="(item, index) in districtList"
          :key="item.adcode"
          :label="item.name"
          :value="index">
        </el-option>
      </el-select>
      <el-select v-model="streetIndex" v-on:change="search(streetIndex, 'street')" placeholder="请选择街道">
        <el-option
          v-for="(item, index) in streetList"
          :key="item.adcode"
          :label="item.name"
          :value="index">
        </el-option>
      </el-select>
      <el-button type="primary" icon="el-icon-download" v-on:click="downloadGeo">下载</el-button>
    </div>
    <a href=""></a>
  </div>
</template>

<script>
import AMap from 'AMap'
var map, districtSearch
var polygons = []
export default {
  name: 'aMap',
  data () {
    return {
      bounds: [],
      provinceIndex: '',
      cityIndex: '',
      districtIndex: '',
      streetIndex: '',
      city: '',
      district: '',
      street: '',
      provinceList: [],
      cityList: [],
      districtList: [],
      streetList: []
    }
  },
  methods: {
    init: function () {
      map = new AMap.Map('amap-container', {
        center: [116.397428, 39.90923]
      })
      map.setZoom(14)
      // 初始化行政区划搜索
      AMap.service('AMap.DistrictSearch', function () {
        // 在对象初始化的时候设定
        districtSearch = new AMap.DistrictSearch({
          subdistrict: 1, // 返回下一级行政区
          showbiz: false // 最后一级返回街道信息
        })
      })
      // 初始化省列表
      let _ = this
      districtSearch.search('中国', function (status, result) {
        if (status === 'complete') {
          _.getData(result.districtList[0])
        }
      })
    },
    getData (data, level) {
      let _ = this
      _.bounds = []
      _.bounds = data.boundaries
      if (_.bounds) {
        var len = _.bounds.length
        for (let i = 0; i < len; i++) {
          var polygon = new AMap.Polygon({
            map: map,
            strokeWeight: 1,
            strokeColor: '#CC66CC',
            fillColor: '#CCF3FF',
            fillOpacity: 0.5,
            path: _.bounds[i]
          })
          polygons.push(polygon)
        }
        map.setFitView() // 地图自适应
      }
      // 清空下一级别的下拉列表
      if (level === 'province') {
        _.cityList = []
        _.districtList = []
        _.streetList = []
      } else if (level === 'city') {
        _.districtList = []
        _.streetList = []
      } else if (level === 'district') {
        _.streetList = []
      }
      var subList = data.districtList
      if (subList) {
        var curLevel = subList[0].level
        if (curLevel === 'province') {
          _.provinceList = subList
        }
        if (curLevel === 'city') {
          _.cityList = subList
        }
        if (curLevel === 'district') {
          _.districtList = subList
        }
        if (curLevel === 'street') {
          _.streetList = subList
        }
      }
    },
    search: function (index, level) {
      let _ = this
      // 清除地图上所有覆盖物
      for (let i = 0, l = polygons.length; i < l; i++) {
        polygons[i].setMap(null)
      }
      var d, adcode
      if (level === 'province') {
        d = _.provinceList[index]
      }
      if (level === 'city') {
        d = _.cityList[index]
      }
      if (level === 'district') {
        d = _.districtList[index]
      }
      if (level === 'street') {
        d = _.streetList[index]
      }
      // 标注
      // map.clearMap()
      // var marker = new AMap.Marker({
      //   // content: '<div style="padding: 5px 8px; border-radius: 3px; color: #fff; background-color: rgba(0,0,0,.5);">' + d.name + '</div>',
      //   content: d.name,
      //   position: d.center.toString().split(','),
      //   offset: new AMap.Pixel(0, 0)
      // })
      // marker.setMap(map)
      adcode = d.adcode
      districtSearch.setLevel(level) // 行政区级别
      districtSearch.setExtensions('all')
      // 行政区查询
      // 按照adcode进行查询可以保证数据返回的唯一性
      districtSearch.search(adcode, function (status, result) {
        if (status === 'complete') {
          _.getData(result.districtList[0], level)
        }
      })
    },
    downloadGeo: function () {
      var coods = [] // 坐标
      var b = this.bounds // 边界坐标
      for (let i = 0; i < b.length; i++) {
        const e = b[i]
        for (let j = 0; j < e.length; j++) {
          const cood = e[j]
          coods.push([cood.lng, cood.lat])
        }
      }
      var res = {
        'type': 'FeatureCollection',
        'features': [
          {
            'type': 'Feature',
            'properties': {},
            'geometry': {
              'type': 'Polygon',
              'coordinates': [coods]
            }
          }
        ]
      }
      var blob = new Blob([JSON.stringify(res)], {
        type: 'text/plain;charset=utf-8'
      })
      saveFile(blob, 'coordinate.geojson')
    }
  },
  mounted: function () {
    this.init()
  }
}
</script>

<style scoped>
  .container, #amap-container {
    width: 100%;
    height: 100%;
  }
  .citySelect-container {
    position: fixed;
    top: 10px;
    left: 10px;
    z-index: 9;
  }
</style>



